* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  background: rgb(40, 40, 40);
}
.container {
  padding: 20px;
}

a {
  color: #333;
  text-decoration: none;
}
.btn {
  background-color: rgb(90, 90, 90);
  color: white;
  border: 1px solid rgb(40, 40, 40);
  height: 20px;
  width: 20px;
  padding: 2px 2px;
  border-radius: 50%;
  align-self: center;
  display: flex;
  justify-content: center;
  cursor: pointer;
  position: absolute;
  right: 29px;
}
.btn:hover {
  color: yellowgreen;
}
.todo-item {
  background: rgb(50, 50, 50);
  padding: 10px;
  margin: 7px;
  border-radius: 8px;
  border-bottom: 1px solid rgb(10, 10, 10, 0.2);
  box-shadow: 0px 0px 3px rgba(255, 255, 255, 0.1);
  /* width: 100%; */
  display: flex;
  position: relative;
}
